.product-list{
    padding:20px;
}
.product-item{
    margin: 10px 0;
    box-shadow: 0 0 4px 2px rgba(0,0,0,0.2);
}
.tag{
    display: flex;
    flex-wrap: wrap;
}
.product-item-tag{
    font-size: 10px;
    padding:3px;
    margin: 10px 3px;
    white-space: nowrap;
    border-radius: 4px;
    border: 1px solid black;
}